.docs-callout {
  padding: 1.25rem;
  margin-top: 1.25rem;
  margin-bottom: 1.25rem;
  border: 1px solid #eee;
  border-left-width: .25rem;
  border-radius: .25rem;
}

.docs-callout h4 {
  margin-top: 0;
  margin-bottom: .25rem;
}

.docs-callout p:last-child {
  margin-bottom: 0;
}

.docs-callout code {
  border-radius: .25rem;
}

.docs-callout + .docs-callout {
  margin-top: -.25rem;
}

.docs-callout-info {
  border-left-color: #17a2b8;
}

.docs-callout-info h4 {
  color: #17a2b8;
}

.docs-callout-warning {
  border-left-color: #ffc107;
}

.docs-callout-warning h4 {
  color: #ffc107;
}

.docs-callout-danger {
  border-left-color: #dc3545;
}

.docs-callout-danger h4 {
  color: #dc3545;
}

.docs-content {
  -webkit-box-ordinal-group: 2;
      -ms-flex-order: 1;
          order: 1;
}

.docs-content > table {
  width: 100%;
  max-width: 100%;
  margin-bottom: 1rem;
}

@media (max-width: 991px) {
  .docs-content > table {
    display: block;
    overflow-x: auto;
    -ms-overflow-style: -ms-autohiding-scrollbar;
  }
}

.docs-content > table > thead > tr > th,
.docs-content > table > thead > tr > td,
.docs-content > table > tbody > tr > th,
.docs-content > table > tbody > tr > td,
.docs-content > table > tfoot > tr > th,
.docs-content > table > tfoot > tr > td {
  padding: 0.75rem;
  vertical-align: top;
  border: 1px solid #e9ecef;
}

.docs-content > table > thead > tr > th > p:last-child,
.docs-content > table > thead > tr > td > p:last-child,
.docs-content > table > tbody > tr > th > p:last-child,
.docs-content > table > tbody > tr > td > p:last-child,
.docs-content > table > tfoot > tr > th > p:last-child,
.docs-content > table > tfoot > tr > td > p:last-child {
  margin-bottom: 0;
}

.docs-content > table td:first-child > code {
  white-space: nowrap;
}

